import React from 'react';
import AbstractList from '../Core/AbstractList'
import moment from 'jalali-moment'


const ButtonStyle = {
    marginLeft: "10px"
}

export default class MyOrderList extends AbstractList {


	getEntityName() {
		return "myOrder";
	}

	getSearchPanel() {
		return [
			{ id: "EQ:title", type: 'text', label: '' },
			{ id: "EQ:goods.id", type: 'combo', label: '',
				values: this.state.options ? this.state.options.products : [],
				convertToVal: it => it.id, convertToStr: it => it.title },
			{ id: "EQ:currentState.id", type: 'combo', label: '',
				values: this.state.options ? this.state.options.states : [],
				convertToVal: it => it.id, convertToStr: it => it.title }
		]
	}

	getTableHead() {
		return ["", "" , " ", "", "", ""];
	}

	createAble() {
        return false;
	}

	convertToTableRow( data ) {
		let price = 0;
		data.items.forEach( item => price += item.price );
		return (
			<tr key={"item_" + data.id}>
				<td>{data.id}</td>
				<td>{data.title}</td>
				<td>{moment(data.createDate).locale('fa').format('YYYY/MM/DD')}</td>
				<td>{data.currentState ? data.currentState.title : ""}</td>
				<td>{price}</td>
				<td>{ data.editable && <a style={ButtonStyle} href="javascript:void(0);"><i className="fa fa-pencil" aria-hidden="true"></i></a> }</td>
			</tr>
		);
	}

}



